.actual {
	&__grid {
		display: grid;
		grid-gap: var(--gap);

		@media (max-width: 1024px) {
			grid-gap: 3rem;
		}

		@media (max-width: 500px) {
			grid-gap: 2rem;
		}

		@media (min-width: 768px) {
			grid-template-columns: repeat(2, 1fr);
		}

		@media (min-width: 1025px) {
			grid-template-columns: repeat(4, 1fr);
		}
	}

	&__item {
		&:first-child {
			@media (min-width: 768px) {
				grid-column: span 2;
			}
		}
	}
}
