.page {
	position: relative;
	flex: 1 0 auto; // Footer at bottom
	padding-bottom: 11rem;

	@media (max-width: 1024px) {
		padding-bottom: 0rem;
	}

	@media (max-width: 767px) {
		padding-top: 2.4rem;
	}

	@media (max-width: 500px) {
		padding-bottom: 3rem;
	}

	&__top {
		margin-bottom: 4rem;

		&--margin-bottom {
			&_30 {
				margin-bottom: 3rem;
			}
		}

		@media (max-width: 500px) {
			margin-bottom: 2rem;
		}
	}

	&__subtitle {
		@extend %text-20;
		font-weight: 500;

		@media (max-width: 500px) {
			font-size: 1.4rem;
			line-height: 2.1rem;
		}

		&:not(:last-child) {
			margin-bottom: 5.2rem;

			@media (max-width: 500px) {
				margin-bottom: 2.5rem;
			}
		}
	}
}

.home-page {
	padding-top: 1.5rem;

	>* {
		&:first-child {
			padding-bottom: 7rem;
		}
	}

	&__seo-text {
		@media (min-width: 1025px) {
			max-width: 111.2rem;
		}
	}
}
