.catalog-home {
	padding-top: 1.5rem;
	// .catalog-home__grid

	&__grid {
		display: grid;
		grid-gap: 2rem;

		&:not(:last-child) {
			margin-bottom: 11rem;

			@media (max-width: 1024px) {
				margin-bottom: 7rem;
			}

			@media (max-width: 767px) {
				margin-bottom: 3rem;
			}
		}

		@media (min-width: 768px) {
			grid-template-columns: repeat(3, 1fr);
			grid-gap: 3rem;
		}

		@media (min-width: 1025px) {
			grid-gap: var(--gap);
		}
	}
}

.catalog-home-card {
	position: relative;
	height: 61rem;

	@media (max-width: 1024px) {
		height: 35rem;
	}

	@media (max-width: 767px) {
		height: 55rem;
	}

	@media (max-width: 500px) {
		height: 30rem;
	}

	@media (any-hover: hover) {
		&:hover {
			.catalog-home-card__title {
				height: 100%;
			}
		}
	}

	// .catalog-home-card__title

	&__title {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		background-color: rgba(var(--dark), 0.6);
		padding: 0 2.4rem;
		@extend %flex-center;
		text-align: center;
		@extend %text-25;
		font-weight: 500;
		color: #fff;
		height: 8.6rem;
		transition: 0.5s height;

		@media (max-width: 1024px) {
			height: 6rem;
			font-size: 2rem;
			line-height: 3rem;
		}
	}

	&__img {
		img {
			@extend %cover;
		}
	}
}
